
import React, { useState, useEffect } from "react";
import { withLDConsumer } from "launchdarkly-react-client-sdk";
import {
  MainContainer,
  ChatContainer,
  MessageList,
  Message,
  MessageInput,
  TypingIndicator,
  Avatar
} from '@chatscope/chat-ui-kit-react';
// CSS import removed - you can add custom styles if needed
import axios from 'axios';
import niceRobot from "./../images/happyRobot.png";
import meanRobot from "./../images/meanRobot.png";
import pirateRobot from "./../images/pirateRobot.png";

const Chatbot = ({ flags, ldClient }) => {
  const [messages, setMessages] = useState([
    {
      message: "Hi there! Ask me a question!",
      sentTime: "just now",
      sender: "bot",
      direction: "incoming"
    }
  ]);
  const [isTyping, setIsTyping] = useState(false);
  
  let personality = ldClient.variation("config-chatbot-personality");

  function changeAvatar() {
    if (personality === 'pirate') {
      return pirateRobot
    } else if (personality === 'mean') {
      return meanRobot
    } else {
      return niceRobot
    }
  }

  function changeTitle() {
    if (personality === 'pirate') {
      return 'ARRRRobot'
    } else if (personality === 'mean') {
      return 'Stack Overflow Bot'
    } else {
      return 'Answerbot'
    }
  }

  const url = 'https://fxbft3m9yf.execute-api.us-east-2.amazonaws.com/default/answer?question='

  async function sendRequest(url) {
    if (personality === 'placeholder') {
      return "Error: Bot not found."
    } else {
      const response = await axios.get(url);
      return response.data.answer;
    }
  }

  function URLify(string) {
    const question = string.trim().replace(/\s/g, '%20');
    return url + question
  }

  const handleSend = async (message) => {
    const newMessage = {
      message,
      direction: 'outgoing',
      sender: "user"
    };

    const newMessages = [...messages, newMessage];
    setMessages(newMessages);
    setIsTyping(true);
    
    try {
      const response = await sendRequest(URLify(message));
      const botMessage = {
        message: response,
        sender: "bot",
        direction: "incoming"
      };
      
      setMessages([...newMessages, botMessage]);
    } catch (error) {
      const errorMessage = {
        message: "Sorry, I encountered an error. Please try again.",
        sender: "bot", 
        direction: "incoming"
      };
      setMessages([...newMessages, errorMessage]);
    }
    
    setIsTyping(false);
  };

  return flags.showChatbot ? (
    <div style={{ position: "relative", height: "400px", width: "300px", margin: "auto" }}>
      <MainContainer>
        <ChatContainer>
          <MessageList
            typingIndicator={isTyping ? <TypingIndicator content={`${changeTitle()} is typing`} /> : null}
          >
            {messages.map((message, i) => (
              <Message 
                key={i} 
                model={message}
              >
                <Avatar src={message.sender === "bot" ? changeAvatar() : undefined} name={message.sender} />
              </Message>
            ))}
          </MessageList>
          <MessageInput placeholder="Ask a question" onSend={handleSend} />
        </ChatContainer>
      </MainContainer>
    </div>
  ) : (
    <div></div>
  );
};

export default withLDConsumer()(Chatbot);